
body {
  font: 12/1.5 "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
  color: #333;
}
.vui-warper {
  width: 100%;
  min-width: 80%;
}
.vui-warper-content {
  width: 80%;
  margin: 0 auto;
}
.vui-content {
  margin: 0 auto;
  position: relative;
}
.vui-menu {
  position: absolute;
  top: 0;
  background: #DDE0E2;
  width: 280px;
  margin-right: 20px;
}
.vui-main {
  padding: 20px 0 20px 300px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  min-height: 300px;
}
.vui-clear {
  zoom: 1;
}
.vui-clear:before,
.vui-clear:after {
  display: table;
  content: "";
}
.vui-clear:after {
  clear: both;
}
.menu-list {
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  text-indent: 1em;
}
.menu-list:hover {
  text-decoration: underline;
}
.menu-active {
  background: #795da3;
  color: #fff;
}



