@import url("../iconfont/iconfont.css");
html,
body {
  width: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-overflow-scrolling: touch;
  overflow-scrolling: touch;
}
html,
body,
div,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
img,
fieldset,
hr,
legend,
p,
pre,
table,
thead,
tbody,
th,
td,
th,
form,
input,
select,
textarea,
button {
  margin: 0;
  padding: 0;
  outline: none;
}
body {
  /*make the font look like to smoothly*/
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
  font: 12px/1.5 "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
}
/*get attributes form parent*/
a,
h1,
h2,
h3,
h4,
h5,
h6,
input,
select,
button,
option,
textarea,
optgroup {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  font-style: inherit;
  line-height: inherit;
  color: inherit;
  outline: none;
}
a {
  text-decoration: none;
  cursor: pointer;
}
a:active,
a:hover {
  outline: none;
}
a:focus {
  outline: none;
  -moz-outline: none;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
img {
  vertical-align: middle;
  -ms-interpolation-mode: bicubic;
  border: none;
}
ul,
li,
ol {
  list-style: none;
}
i,
em,
b,
strong {
  font-weight: normal;
  font-style: normal;
}
input,
select,
textarea,
button {
  /*make el can get attributes form parent on IE */
  font-size: 100%;
  border: none;
  background: none;
  appearance: none;
  -moz-appearance: none;
  -webkit-appearance: none;
  /*fixed Box model isn't same*/
  box-sizing: content-box;
}
input::-moz-focus-inner,
button::-moz-focus-inner {
  /*fixed outline not work in firefox*/
  border: 0;
  padding: 0;
}
textarea {
  overflow-y: auto;
  resize: none;
}
a,
input[type="button"],
input[type="submit"],
input[type="reset"],
button {
  cursor: pointer;
}
input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  /* WebKit browsers */
  color: #666;
}
input:-moz-placeholder,
textarea:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  opacity: 1;
  color: #666;
}
input:-ms-input-placeholder,
textarea:-ms-input-placeholder {
  /* Internet Explorer 10+ */
  color: #666;
}
select::-ms-expand {
  /*fixed IE*/
  display: none;
}
/* project style */
/* m_表示模块 */
/* f_表示功能 */
/* u_表示UI */
.m_out {
  width: 100%;
  min-width: 1080px;
}
.m_in {
  width: 1080px;
  margin: 0 auto;
}
.f_clear {
  zoom: 1;
}
.f_clear:before,
.f_clear:after {
  display: table;
  content: "";
}
.f_clear:after {
  clear: both;
}
.f_dib {
  display: inline-block;
  zoom: 1;
  *display: inline;
  _display: inline;
}
.f_dbk {
  display: block;
}
.f_hide {
  display: none;
}
.f_tal {
  text-align: left;
}
.f_tac {
  text-align: center;
}
.f_tar {
  text-align: right;
}
.f_filter {
  opacity: 0.5;
  filter: alpha(opacity=50);
  -moz-opacity: 0.5;
  -khtml-opacity: 0.5;
}
.f_hdmx {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.f_float {
  display: inline-block;
  zoom: 1;
  *display: inline;
  _display: inline;
  text-spacing: -3px;
}
.f_left {
  float: left;
}
.f_right {
  float: right;
}
/*修复margin-top被父元素‘继承’问题*/
.f_topbdr {
  border-top: 1px solid transparent;
}
.f_disselected {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.box_shadow {
  box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.3);
  -moz-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.3);
  /*firefox*/
  -webkit-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.3);
  /*webkit*/
}
.box_shadow_2 {
  box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.2);
  -moz-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.2);
  /*firefox*/
  -webkit-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.2);
  /*webkit*/
}
.icon {
  vertical-align: top;
  display: inline-block;
  zoom: 1;
  *display: inline;
  _display: inline;
}
[v-cloak] {
  display: none;
}
.vui-left {
  float: left;
}
.vui-right {
  float: right;
}
.vui-clear {
  zoom: 1;
}
.vui-clear:before,
.vui-clear:after {
  display: table;
  content: "";
}
.vui-clear:after {
  clear: both;
}
.vui-dis-selected {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.vui-title {
  font-size: 14px;
  color: #333;
  height: 40px;
  line-height: 40px;
}
.vui-box-shadow {
  box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.3);
  -moz-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.3);
  /*firefox*/
  -webkit-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.3);
  /*webkit*/
}
.vui-box-shadow-2 {
  box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.2);
  -moz-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.2);
  /*firefox*/
  -webkit-box-shadow: 0px 1px 5px 0px rgba(9, 2, 4, 0.2);
  /*webkit*/
}
.vui-font-title-main {
  font-weight: bold;
  font-size: 16px;
  color: #333;
}
.vui-font-title-less {
  font-weight: bold;
  font-size: 14px;
  color: #333;
}
.vui-font-title-mini {
  font-weight: bold;
  font-size: 12px;
  color: #333;
}
.vui-font-content {
  font-size: 12px;
  color: #333;
}
.vui-font-hand {
  font-size: 12px;
  color: #666;
}
.vui-font-invalid {
  font-size: 12px;
  color: #999;
}
.vui-font-link {
  font-size: 18px;
  color: #2d8cf0;
}
.vui-font-link:hover {
  color: #267fed;
  cursor: pointer;
}
.vui-shade {
  z-index: 999999;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.3);
}
.vui-modal {
  position: relative;
  width: 350px;
  top: 50%;
  margin: -100px auto;
  background: #fff;
  text-align: center;
  border-radius: 5px;
}
.vui-modal .vui-modal-header {
  border-bottom: 1px solid #e6e6e6;
}
.vui-modal .vui-modal-header .vui-modal-title {
  font-size: 14px;
  color: #333;
  height: 40px;
  line-height: 40px;
  margin-left: 20px;
  text-align: left;
}
.vui-modal .vui-modal-header .vui-close {
  text-align: center;
  display: inline-block;
  vertical-align: top;
  cursor: pointer;
  width: 40px;
  font-size: 18px;
}
.vui-modal .vui-modal-body {
  padding: 20px;
}
.vui-modal .vui-modal-footer {
  position: relative;
  bottom: 0;
  height: 60px;
  line-height: 60px;
  border-top: 1px solid #e6e6e6;
  padding-top: 10px;
}
.vui-button {
  display: inline-block;
  text-align: center;
  border-radius: 5px;
  border: 1px solid transparent;
  cursor: pointer;
  box-sizing: border-box;
}
.vui-button.vui-button-small {
  padding: 0 10px;
  height: 30px;
  line-height: 30px;
  font-size: 12px;
}
.vui-button.vui-button-middle {
  padding: 0 15px;
  height: 35px;
  line-height: 35px;
  font-size: 14px;
}
.vui-button.vui-button-larger {
  padding: 0 25px;
  height: 45px;
  line-height: 45px;
  font-size: 16px;
}
.vui-button:disabled,
.vui-button:disabled:hover {
  background: #e5e5e5;
  border-color: #d9d9d9;
  color: #fff;
  cursor: not-allowed;
}
.vui-button-default {
  background: #f7f7f7;
  border: 1px solid #d9d9d9;
  color: #666;
}
.vui-button-default:hover {
  border: 1px solid #8766ff;
  color: #8766ff;
}
.vui-button-primary {
  background: #8766ff;
  color: #fff;
}
.vui-button-primary:hover {
  background: #8766ff;
}
.vui-button-ghost {
  border: 1px solid #d9d9d9;
  color: #666;
}
.vui-button-ghost:hover {
  border: 1px solid #8766ff;
  color: #8766ff;
}
.vui-button-dashed {
  border: 1px dashed #d9d9d9;
  color: #666;
}
.vui-button-dashed:hover {
  border: 1px dashed #8766ff;
  color: #8766ff;
}
.vui-input {
  box-sizing: border-box;
  width: 100px;
  height: 30px;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 0 10px;
}
.vui-input:disabled,
.vui-input:read-only {
  background: #f2f2f2;
}
[data-tips] {
  position: relative;
}
[data-tips]:hover:before {
  content: attr(data-tips);
  position: absolute;
  z-index: 999;
  top: -100%;
  left: 50%;
  margin-left: -50%;
  margin-top: -15px;
  display: block;
  min-width: 100%;
  padding: 0 10px;
  pointer-events: none;
  white-space: nowrap;
  font-size: 14px;
  color: #fff;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 5px;
  -webkit-transition: all ease-in-out 1s;
  -moz-transition: all ease-in-out 1s;
  transition: all ease-in-out 1s;
}
[data-tips]:hover:after {
  content: "";
  position: absolute;
  top: -15px;
  left: 50%;
  margin-left: -7px;
  display: block;
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-top: 10px solid rgba(0, 0, 0, 0.8);
  border-right: 7px solid transparent;
}



